/**
 * @name 
 *
 * @class TextNodeQueue
 *
 * @constructor
 * @param {Object} config Darkmode
 * @param {string} prefix 
 *
 * @method push 
 * @param {Dom Object} el 
 *
 * @method forEach 
 * @param {function} callback 
 *
 * @method update 
 * @param {Dom Object Array} nodes 
 *
 */

export default class TextNodeQueue {
  _queue = []; // { el, className, updated }
  _idx = 0; // 

  constructor(config, prefix) {
    this._config = config;
    this._prefix = prefix;
  }

  push(el) {
    const className = `${this._prefix}${this._idx++}`;
    el.classList.add(className);
    this._queue.push({
      el,
      className,
      updated: !this._config.delayBgJudge
    });
  }

  forEach(callback) {
    const idxStack = [];

    this._queue.forEach((item, idx) => {
      if (item.updated) {
        idxStack.unshift(idx);
        typeof callback && callback(item.el);
      }
    });

    while (idxStack.length) { // 
      this._queue.splice(idxStack.shift(), 1);
    }
  }

  update(nodes) {
    this._queue.forEach(item => {
      if (!item.updated) {
        Array.prototype.some.call(nodes, node => {
          if (node.classList.contains(item.className)) {
            item.el = node;
            item.updated = true;
            return true;
          }
          return false;
        });
      }
    });
  }
};