/**
 * @name Darkmode
 *
 * @function run 
 * @param {Dom Object Array} nodes 
 * @param {Object}           opt   Dark Modeinit
 *
 * @function init Dark Mode
 * @param {Function}   opt.error              error
 * @param {String}     opt.mode               (dark|light), 
 * @param {Object}     opt.whitelist          
 * @param {Array}      opt.whitelist.tagName  
 * @param {Boolean}    opt.needJudgeFirstPage 
 * @param {Boolean}    opt.delayBgJudge       
 * @param {DOM Object} opt.container          js
 *
 * @function convert 
 * @param {Dom Object Array} nodes 
 * @param {Boolean}          force 
 *
 * @function convertBg 
 * @param {Dom Object Array} nodes 
 * @param {Boolean}          force 
 *
 */

import {
  MEDIA_QUERY,

  CLASS_PREFIX,

  PAGE_HEIGHT
} from './common/constant';
const classReg = new RegExp(`${CLASS_PREFIX}[^ ]+`, 'g');

// Darkmode
const config = {
  error: null, // error
  mode: '', // (dark|light), 
  whitelist: { // 
    tagName: ['MPCPS', 'IFRAME'] // 
  },
  needJudgeFirstPage: true, // 
  delayBgJudge: false, // 
  container: null // js
};

// 
import TextNodeQueue from './common/textNodeQueue';
const tnQueue = new TextNodeQueue(config, `${CLASS_PREFIX}text__`);

// 
import BgNodeStack from './common/bgNodeStack';
const bgStack = new BgNodeStack(config, `${CLASS_PREFIX}bg__`);

// 
import CssUtils from './common/cssUtils';
const cssUtils = new CssUtils(config);

// 
import {
  DomUtils
} from './common/domUtils';
const domUtils = new DomUtils(config);

import SDK from './common/sdk';
const sdk = new SDK({
  config,
  tnQueue,
  bgStack,
  cssUtils
});

// Dark Mode
let mql = null;
const switchToDarkmode = (mqlObj, opt = {
  type: 'dom'
}) => {
  opt.force && (cssUtils.isFinish = false); // Dark Mode

  if (cssUtils.isFinish) return; // Dark Mode

  try {
    if (cssUtils.mode ? (cssUtils.mode === 'dark') : mqlObj.matches) { // Dark Mode
      if (opt.type === 'dom') { // 
        domUtils.get().forEach(node => {
          if (node.className && typeof node.className === 'string') {
            node.className = node.className.replace(classReg, ''); // Dark Mode classDark Mode classDark Mode
          }

          if (!config.needJudgeFirstPage) { // 
            cssUtils.addCss(sdk.convert(node), false); // 
          } else { // 
            const rect = node.getBoundingClientRect();
            const top = rect.top;
            const bottom = rect.bottom;
            if (top <= 0 && bottom <= 0) { // 
              cssUtils.addCss(sdk.convert(node), false); // 
            } else if ((top > 0 && top < PAGE_HEIGHT) || (bottom > 0 && bottom < PAGE_HEIGHT)) { // 
              domUtils.addFirstPageNode(node); // 
              cssUtils.addCss(sdk.convert(node), true); // 
            } else { // 
              config.needJudgeFirstPage = false; // 

              // 
              cssUtils.writeStyle(true); // 
              domUtils.showFirstPageNodes(); // 

              cssUtils.addCss(sdk.convert(node), false); // 
            }
          }
        });
      } else if (opt.type === 'bg') { // 
        tnQueue.forEach(text => bgStack.contains(text, bg => {
          cssUtils.addCss(cssUtils.genCss(bg.className, bg.cssKV), false); // 
        }));
      }
    } else {
      // Dark Mode
      config.needJudgeFirstPage = false;

      // Dark Mode
      config.delayBgJudge = false;

      if (config.container === null && opt.type === 'dom' && domUtils.len()) {
        domUtils.delay(); // 
      }
    }

    cssUtils.writeStyle(); // 
  } catch (e) {
    console.error(e);
    typeof config.error === 'function' && config.error(e);
  }
};

export function run(nodes, opt) {
  init(opt); // 
  convert(nodes); // 
};

export function init(opt = {}) {
  opt = JSON.parse(JSON.stringify(opt)); // 

  const whitelist = opt.whitelist || {};
  delete opt.whitelist;

  Object.assign(config, opt);

  // whitelist.tagName
  const tagName = config.whitelist.tagName;
  whitelist.tagName && whitelist.tagName.forEach(item => {
    item = item.toUpperCase();
    tagName.indexOf(item) === -1 && tagName.push(item);
  });

  if (!cssUtils.mode && mql === null) {
    // 
    mql = window.matchMedia(MEDIA_QUERY);
    mql.addListener(switchToDarkmode); // 
  }
};

export function convert(nodes, force = false) {
  domUtils.set(nodes);

  switchToDarkmode(mql, {
    force,
    type: 'dom'
  });
};

export function convertBg(nodes, force = false) {
  domUtils.set(nodes);

  if (config.container !== null) {
    bgStack.update(nodes); // 
    tnQueue.update(nodes); // 
  }

  switchToDarkmode(mql, {
    force,
    type: 'bg'
  });
};