/**
 * @name 
 *
 * @class BgNodeStack
 *
 * @constructor
 * @param {Object} config Darkmode
 * @param {string} prefix 
 *
 * @method push 
 * @param {Dom Object} el    
 * @param {string}     cssKV css
 *
 * @method contains 
 * @param {Dom Object} el       
 * @param {function}   callback 
 *
 * @method update 
 * @param {Dom Object Array} nodes 
 *
 */

export default class BgNodeStack {
  _stack = []; // { el, className, cssKV, updated, rect }
  _idx = 0; // 

  constructor(config, prefix) {
    this._config = config;
    this._prefix = prefix;
  }

  push(el, cssKV) {
    const className = `${this._prefix}${this._idx++}`;
    el.classList.add(className);
    this._stack.unshift({
      el,
      className,
      cssKV,
      updated: !this._config.delayBgJudge
    });
  }

  contains(el, callback) {
    const rect = el.getBoundingClientRect();
    const idxStack = [];

    this._stack.forEach((item, i) => {
      if (item.updated) {
        if (!item.rect) item.rect = item.el.getBoundingClientRect();

        const bgRect = item.rect;
        if (!(rect.top >= bgRect.bottom || rect.bottom <= bgRect.top || rect.left >= bgRect.right || rect.right <= bgRect.left)) { // 
          idxStack.unshift(i);
        }
      }
    });

    while (idxStack.length) {
      const item = this._stack.splice(idxStack.shift(), 1)[0];
      typeof callback === 'function' && callback(item);
    }
  }

  update(nodes) {
    this._stack.forEach(item => {
      if (!item.updated) {
        Array.prototype.some.call(nodes, node => {
          if (node.classList.contains(item.className)) {
            item.el = node;
            item.updated = true;
            return true;
          }
          return false;
        });
      }
    });
  }
};