/**
 * @name API
 *
 * @function getChildrenAndIt 
 * @param {Dom Object} dom 
 * @returns {Dom Object Array} 
 *
 * @function hasTextNode 
 * @param {Dom Object} dom 
 * @returns {boolean} 
 *
 * @function hasTableClass tableclass
 * @param {Dom Object} dom 
 * @returns {string | null} classlmnull
 *
 */

export function getChildrenAndIt(dom) {
  return [dom].concat(...dom.querySelectorAll('*'));
};

export function hasTextNode(dom) {
  let cnt = '';
  dom.childNodes.forEach(node => {
    if (node.nodeType === 3) {
      cnt += node.nodeValue.replace(/\s/g, ''); // 
    }
  });
  return cnt.length > 0;
};

// tablelm
const tableClassObj = {
  'ue-table-interlace-color-single': '#fcfcfc',
  'ue-table-interlace-color-double': '#f7faff'
};
export function hasTableClass(dom) {
  let color = null;

  Array.prototype.some.call(dom.classList, className => {
    if (tableClassObj[className]) {
      color = tableClassObj[className];
      return true;
    }
    return false;
  });

  return color;
};

/**
 * @name 
 *
 * @class DomUtils
 *
 * @constructor
 * @param {Object} config Darkmode
 *
 * @method set 
 * @param {Dom Object Array} nodes 
 *
 * @method len 
 * @returns {number} 
 *
 * @method get 
 * @returns {Dom Object Array} 
 *
 * @method delay 
 *
 * @method addFirstPageNode 
 * @param {Dom Object} node 
 *
 * @method showFirstPageNodes 
 *
 */

export class DomUtils {
  _nodes = []; // 
  _firstPageNodes = []; // 
  _delayNodes = []; // 

  constructor(config) {
    this._config = config;
  }

  set(nodes = []) {
    this._nodes = nodes;
  }

  len() {
    return this._nodes.length;
  }

  get() {
    let res = [];

    if (this._nodes.length) { // 
      res = this._nodes;
      this._nodes = [];
    } else { // 
      if (this._delayNodes.length) { // 
        res = this._delayNodes;
        this._delayNodes = [];
      } else if (this._config.container) { // 
        res = this._config.container.querySelectorAll('*');
      }
    }

    return res;
  }

  delay() {
    this._nodes.forEach(node => this._delayNodes.push(node)); // 
    this._nodes = []; // 
  }

  addFirstPageNode(node) {
    this._firstPageNodes.push(node);
  }

  showFirstPageNodes() {
    this._firstPageNodes.forEach(node => node.style.visibility = 'visible'); // 
    this._firstPageNodes = []; // 
  }
};