/**
 * @name 
 *
 * @class CssUtils
 *
 * @constructor
 * @param {Object} config Darkmode
 *
 * @attr {boolean} isFinish Dark Mode
 *
 * @method genCssKV css
 * @param {string} key css
 * @param {string} val css
 * @returns {string} css
 *
 * @method genCss csscss
 * @param {string} className DOM
 * @param {string} cssKV     css
 * @returns {string} css
 *
 * @method addCss css
 * @param {string}  css              css
 * @param {boolean} isFirstPageStyle 
 *
 * @method writeStyle 
 * @param {boolean} isFirstPageStyle 
 *
 */

import {
  MEDIA_QUERY,
  HTML_CLASS,
} from './constant';

export default class CssUtils {
  _firstPageStyle = ''; // 
  _otherPageStyle = ''; // 

  isFinish = false;

  constructor(config) {
    this._config = config;
  }

  genCssKV(key, val) {
    return `${key}: ${val} !important;`;
  }

  genCss(className, cssKV) {
    return `${this._config.mode === 'dark' ? `html.${HTML_CLASS} ` : ''}.${className}{${cssKV}}`;
  }

  addCss(css, isFirstPageStyle) {
    this[isFirstPageStyle ? '_firstPageStyle' : '_otherPageStyle'] += css;
  }

  writeStyle(isFirstPageStyle) {
    let styleKey = '';

    if (!isFirstPageStyle) { // 
      this._otherPageStyle = this._firstPageStyle + this._otherPageStyle;
      this._firstPageStyle = '';
      styleKey = '_otherPageStyle';
      this.isFinish = true;
    } else {
      styleKey = '_firstPageStyle';
    }

    const style = this[styleKey];

    if (style) {
      document.head.insertAdjacentHTML('beforeend', `<style type="text/css">${this._config.mode === 'dark' ? style : `@media ${MEDIA_QUERY} {${style}}`}</style>`);
      this[styleKey] = ''; // 
    }
  }
};